@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
}

.fixed-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.breadcrump {
  background-image: url(/metalGaseforent/static/media/breadcrumbs.7717bed6.jpg);
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  text-align: center;
}
.breadcrump h1 {
  color: #fff;
}
.inputSearch {
  display: flex;
  justify-content: center;
  padding: 3rem 0;
}

.inputSearch .inputGroup {
  width: 40%;
  position: relative;
}
.inputSearch .inputGroup button {
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: none;
  color: #dc3545;
  font-size: 1.3rem;
}
.inputSearch input {
  display: block;
  width: 100%;
  padding: 0.75rem;
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0.25rem 0 0 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.inputSearch button {
  padding: 0.75rem;
  border: none;
  background: #f9a34f;
}
.main-color {
  color: #f9a34f;
}
.sc-color {
  color: #777;
}

.item-img {
  height: 200px;
  position: relative;
}
.item-img img {
  max-width: 100%;
  height: 200px;
}
.item-img button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
}
.item-info {
  background: #1b232a;
  padding: 40px;
}
.item-info h2,
.item-info p {
  color: #fff;
  text-transform: capitalize;
}
.cartBtn {
  background: none;
  border: 1px solid #f9a34f;
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.1em;
  line-height: 1.6em;
  line-height: 1;
  padding: 23px 26px;
  text-transform: uppercase;
  margin: 0 auto;
}

.addBtn {
  background: none;
  border: 1px solid #0dcaf0;
  color: #0dcaf0;
  display: block;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.1em;
  line-height: 1.6em;
  line-height: 1;
  padding: 23px 26px;
  text-transform: uppercase;
  margin: 0 auto;
}


/* @import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap'); */
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Oswald', sans-serif;
}

.form
{
    position: relative;
    background: #02071b;
    border: 1px solid #000;
    width: 350px;
    padding: 40px 40px 60px;
    border-radius: 10px;
    text-align: center;
}
.form::before
{
    content: '';
    position: absolute;
    top: -4px;
    right: -4px;
    bottom: -4px;
    left: -4px;
    background: linear-gradient(315deg,#e91e63,#5d02ff);
    z-index: -1;
    -webkit-transform: skew(2deg,1deg);
            transform: skew(2deg,1deg);
    border-radius: 10px;
}
.form h2
{
    color: #fff;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 5px;
}
.form .input
{
    margin-top: 40px;
    text-align: left;
}
.form .input .inputBox
{
    margin-top: 10px;
}
.form .input .inputBox label
{
    display: block;
    color: #fff;
    margin-bottom: 5px;
    font-size: 18px;
    letter-spacing: 1px;
}
.form .input .inputBox input
{
    position: relative;
    width: 100%;
    height: 40px;
    border: none;
    outline: none;
    padding: 5px 15px;
    background:linear-gradient(315deg,#54bbe4,#907290) ;
    color: #fff;
    font-size: 18px;
    border-radius: 10px;
}
.form .input .inputBox input[type="submit"]
{
    cursor: pointer;
    margin-top: 20px;
    letter-spacing: 1px;
}
.form .input .inputBox input[type="submit"]:hover
{
    background:linear-gradient(315deg,#5d02ff,#e91e63) ;
}
.form .input .inputBox input[type="submit"]:active
{
    color: rgba(255, 255, 255, 0.521);
    background:linear-gradient(315deg,#e91e6271,#5f02ff8c) ;
}
.forgot
{
    margin-top: 10px;
    color: #fff;
    font-size: 14px;
    letter-spacing: 1px;
}
.forgot a
{
    color: #1becbf;
}
.parent{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}



.exploration-intro {
  position: relative;
  height: 100vh;
}
.exploration-intro .nav-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10000;
}

.exploration-intro .intro-text {
  position: absolute;
  width: 50%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 10px solid #fff;
  text-align: center;
  padding: 2.5rem;
}
.exploration-intro .intro-text h2 {
  color: #fff;
  font-weight: bold;
  font-size: 3.5rem;
  margin-bottom: 1rem;
}
.exploration-intro .intro-text p {
  color: #fff;
  font-size: 1.4rem;
}

.explore-item p {
  text-align: center;
  background: #1b232a;
  color: #999;
  padding: 2rem 3rem;
}

@media (max-width: 1024px) {
  .exploration-intro .intro-text {
    padding: 2rem;
  }
  .exploration-intro .intro-text h2 {
    font-size: 2rem;
  }
}
@media (max-width: 1200px) {
  .exploration-intro .intro-text {
    padding: 2rem;
  }
  .exploration-intro .intro-text h2 {
    font-size: 2rem;
  }
  .exploration-intro .intro-text p {
    font-size: 1.2rem;
  }
}

.Foot {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #1b232a;
  color: #fff;
}
ul.footer-nav,
ul.social {
  list-style: none;
}
ul.footer-nav li,
ul.social li {
  display: inline-block;
}
ul.footer-nav li a {
  text-decoration: none;
  color: #8f9699;
  display: block;
  font-size: 15px;
  padding: 0.5rem;
  margin-right: 10px;
}
ul.footer-nav li a:hover {
  color: #f9a34f;
}
.footer-nav li a i {
  margin-left: 10px;
  font-size: 12px;
}

ul.social li a {
  margin-right: 10px;
}
ul.social li a i {
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
  border: 2px solid rgba(253, 253, 253, 0.1);
  border-radius: 50%;
}

ul.social li a.facebook i {
  color: #3b5998;
}
ul.social li a.twitter i {
  color: #55acee;
}
ul.social li a.google i {
  color: #dd4b39;
}

.nav-container {
  background: #000;
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  font-size: 18px;
  font-weight: 600;
}

.nav-container .logo {
  margin-right: 2rem;
  display: flex;
  align-items: center;
  font-size: 1.6rem;
}
.nav-container .logo img {
  display: inline-block;
  height: 70px;
}
.nav-container .logo div {
  display: inline-block;
}

@media (max-width: 1024px) {
  .nav-container .logo {
    font-size: 1.5rem;
    margin: 0;
  }
  .nav-container .logo img {
    height: 60px;
  }
}

.nav-container .nav-navbar {
  flex: 2 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0;
}
.nav-navbar ul {
  list-style: none;
  margin-bottom: 0;
}
.nav-navbar ul li {
  display: inline-block;
}
.nav-container .nav-link {
  letter-spacing: 3px;
  transition: all 0.3s ease-in-out;
}
.nav-container .nav-link:hover,
.nav-container .nav-link.active {
  color: #f9a34f;
}

.nav-navbar .signBtn {
  color: #00a8ff;
  font-size: 1.5rem;
  text-decoration: none;
  letter-spacing: 0.2rem;
}
.nav-navbar .logoutBtn {
  color: #ff6584;
  text-decoration: none;
  font-size: 1.3rem;
}

.nav-container .nav-link {
  color: #fff;
}

.landing {
  width: 100%;
  height: 100vh;
  background-image: url(/metalGaseforent/static/media/landing.776947fa.jpg);
  background-size: cover;
  background-position: center;
  position: relative;
}
.landing::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.landing .nav-container {
  background: rgba(0, 0, 0, 0.5) !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.landing .container {
  position: relative;
}
.landing-intro {
  flex: 1 1;
  padding-right: 3rem;
}
.landing-intro h1 {
  margin-top: 3rem;
  color: #fff;
  line-height: 1.4;
  font-size: 2.5rem;
}

.landing-info {
  flex: 1 1;
  border-left: 2px solid rgba(144, 144, 144, 0.25);
  padding-left: 3rem;
}
.landing-info p {
  font-size: 1.15rem;
  font-weight: bold;
  line-height: 1.5;
  margin-bottom: 2rem;
  margin-top: 1rem;
  color: #f6f6f6;
  letter-spacing: 0.1rem;
}
.landing-info .landing-buttons a {
  display: inline-block;
  margin-right: 1rem;
  text-decoration: none;
  line-height: 3.5rem;
  padding: 0 3rem;
  color: #fff;
  border-radius: 4px;
  background: #f9a34f;
}
.landing-info .landing-buttons a.explore {
  background: #00bc8a;
}

@media (max-width: 1024px) {
  .landing {
  }
  .landing .container {
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .landing-intro {
    flex: 0 1;
    text-align: center;
    width: 80%;
  }
  .landing-intro h1 {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  .landing-info {
    flex: 0 1;
    width: 80%;
    border: none;
    border-top: 2px solid rgba(144, 144, 144, 0.25);
    text-align: center;
  }
}

@media (max-width: 1200px) {
  .landing .container {
    max-width: 100% !important;
    padding: 2rem;
  }
  .landing-intro h1 {
    font-size: 2rem;
  }
  .landing-info p {
    font-size: 1rem;
  }
}

